
const books = [
    { title: "1. ОРИЕНТИРОВАНИЕ В ПРОСТРАНСТВЕ", link: "/pdf/demo1.pdf" },
    { title: "2. УМЕЕТЕ ЛИ ВЫ ЧИТАТЬ СЛЕДЫ?", link: "/pdf/demo2.pdf" },
    { title: "3. В РАСТИТЕЛЬНОМ МИРЕ (Растения)", link: "/pdf/demo3.pdf" },
]

export function DownloadBook() {

    const items = books.map((book, id) => {
        return (
            <a
                key={id}
                href={book.link}
                download
                target="blank"
                className="list-group-item list-group-item-action ">
                {book.title}
            </a>
        );
    });

    return (
        <div className="row justify-content-md-center">
            <div className="col col-md-6">
                <div className="list-group list-group-flush">
                    {items}
                </div>
            </div>
        </div>
    )
}
export default DownloadBook;
