import { useRouteMatch } from "react-router-dom";

const Menu = [
  {
    title: "ОРИЕНТИРОВАНИЕ В ПРОСТРАНСТВЕ",
    url_target: "/chapter/c001",
    topics: [
      { title: "По компасу, звездам, ветру", url_target: "/topic/t001" },
      { title: "По солнцу (Луне) и часам", url_target: "/topic/t002" },
      { title: "По растениям", url_target: "/topic/t003" },
      { title: "По местным признакам", url_target: "/topic/t004" },
      { title: "Определение расстояний и размеров", url_target: "/topic/t005" },
    ],
  },
  {
    title: "УМЕЕТЕ ЛИ ВЫ ЧИТАТЬ СЛЕДЫ?",
    url_target: "/chapter/c002",
    topics: [
      { title: "Следы человека", url_target: "/topic/t001" },
      { title: "Следы зверей", url_target: "/topic/t002" },
    ],
  },
  {
    title: "ОРИЕНТИРОВАНИЕ В РАСТИТЕЛЬНОМ МИРЕ",
    url_target: "/chapter/c003",
    topics: [
      { title: "Мир растений...", url_target: "/topic/t001" },
      { title: "Мир грибов...", url_target: "/topic/t002" },
    ],
  },
];

export function DataMenu() {

  const { url } = useRouteMatch();

  const chapters = Menu.map((chapter, chapter_id) => {

    const topics = chapter.topics.map((topic, topic_id) => {
      const topic_target = url + chapter.url_target + topic.url_target;
      return (
        <a key={topic_id} href={topic_target} className="list-group-item list-group-item-action ">{topic.title}</a>
      );
    });

    return (
      <div className="card">
        <div className="card-header" id={chapter_id}>
          <h2 className="mb-0">
            <button className="btn btn-block text-left collapsed" type="button" data-toggle="collapse" data-target={"#collapse-" + chapter_id} aria-expanded="false" aria-controls={chapter_id}>
              {chapter.title}
            </button>
          </h2>
        </div>
        <div id={"collapse-" + chapter_id} className="collapse" aria-labelledby={chapter_id} data-parent="#dataMenu">
          <div class="list-group list-group-flush">
            {topics}
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className="accordion pb-3" id="dataMenu">
      {chapters}
    </div>
  );
}

export default DataMenu;
